@import url(https://www.freepik.com/premium-vector/abstract-circle-background-with-glowing-lines_9308812.htm);
@import url(https://fonts.googleapis.com/css2?family=Mulish:wght@200&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.car{
  box-shadow: 10px 10px 5px grey;
  transform: translateY(10px);
  
}

.navbar{
  background-color: #fff;
}

.navbar-brand{
  font-size: 1rem;
  color: #565387;
}

.navbar a{
  font-size: 1.3rem;
  text-transform: capitalize;
}
.menu_active{
  font-weight: bold;
  border-bottom: 1px solid #565387;
}

.navbar a:hover{
  color: #3498db;
}
@media (max-width: 991px){
  .navbar-brand{
    font-size: 1.5rem;
  }
  .menu_active{
    font-weight: bold;
    border-bottom: none;
  }
}

/* Main Landing Section */

#header{
  width: 100%;
  height: 80vh;
  margin-top: 70px;
}

#header h1{
  margin: 0 0 10px 0;
  font-size: 48px;
  font-weight: 70;
  color: black;
}

#header .brand-name{
  color: #3498db;
}

#header h2{
  color: #484848;
}

#header .btn-get-started{
  font-weight: 500;
  font-size: 15px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 10px 30px;
  border-radius: 50px;
  transition: 0.5s;
  color: #3498db;
  border: 2px solid #3498db;
  text-decoration: none;
}

#header .btn-get-started:hover{
  background-color: #3498db;
  color: #fff;
}

.header-img{
  text-align: right;
}

#header .animated{
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
}

@-webkit-keyframes up-down{
  0%{
    transform: translateY(10px);
  }
  100%{
    transform: translateY(-10px);
  }
}

@keyframes up-down{
  0%{
    transform: translateY(10px);
  }
  100%{
    transform: translateY(-10px);
  }
}


